import React, { Component } from 'react';
import { Tween, Timeline, SplitWords, SplitLetters, Controls } from "react-gsap";
import ScrollAnimation from 'react-animate-on-scroll';
import "animate.css/animate.min.css";

class Resume extends Component {
    constructor(props) {
        super(props);
        this.handleEducationHover = this.handleEducationHover.bind(this);
        this.handleExperienceHover = this.handleExperienceHover.bind(this);
        this.state = {
            isEducationHovering: false,
            isExperienceHovering: false,
        };
    }

    handleEducationHover() {
        this.setState(this.toggleEducationHoverState);
    }

    toggleEducationHoverState(state) {
        return {
            isEducationHovering: !state.isEducationHovering,
        };
    }

    handleExperienceHover() {
        this.setState(this.toggleExperienceHoverState);
    }

    toggleExperienceHoverState(state) {
        return {
            isExperienceHovering: !state.isExperienceHovering,
        };
    }

    render() {

        return (
            <div className="page resume-align" id="resume">
                <div className="resume-box-wrapper">
                    <ScrollAnimation animateIn='bounceInUp' animateOut='bounceOutRight'>
                        <div onMouseEnter={this.handleEducationHover}
                            onMouseLeave={this.handleEducationHover}
                            className={this.state.isEducationHovering ? 'expand-resume-box' : 'contract-resume-box'}
                        >
                            <h1 className="resume-box-header">Education</h1>
                            <p className={this.state.isEducationHovering ? 'show-resume-p' : 'hide-resume-p'}>
                                <u>The Ohio State University</u> <i>B.S. in Business Administration</i> <br />
                                <b>Specialization:</b> Accounting  <b>GPA:</b> 3.6 <br />
                                <b>Honors Cohort Program:</b> 1 of 30 out of over 1000 business students selected to participate in a rigorous academic program that focused on leadership skills, team building, real-world experience, and service-learning opportunities <br />
                                <br />
                                <u>Fullstack Academy</u> <i>Software Engineering Student</i> <br />
                                Intensive software development program focusing on JavaScript, Sequelize, Express, React, and related libraries. Created multiple fullstack applications.
                            </p>
                        </div>
                    </ScrollAnimation>
                    <ScrollAnimation animateIn='bounceInDown' animateOut='bounceOutLeft'>
                        <div onMouseEnter={this.handleExperienceHover}
                            onMouseLeave={this.handleExperienceHover}
                            className={this.state.isExperienceHovering ? 'expand-resume-box' : 'contract-resume-box'}
                        >
                            <h1 className="resume-box-header">Experience</h1>
                            <p className={this.state.isExperienceHovering ? 'show-resume-p' : 'hide-resume-p'}>
                                <u>JP Morgan Chase</u> <i>November 2017 - Present</i> <br />
                                Software Engineer<br />
                                <u>PricewaterhouseCoopers</u> <i>August 2015 - May 2017</i> <br />
                                Assurance Associate<br />
                            </p>
                        </div>
                    </ScrollAnimation>
                </div>
            </div>

        );
    }
}

export default Resume;